import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UtilsProvider } from '../../providers/utils/utils';
import { HttpProvider } from '../../providers/http/http';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the AddAccountsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-accounts',
  templateUrl: 'add-accounts.html',
})
export class AddAccountsPage {

  public value: boolean;
  condicion: boolean;
  public selectOptions: any;
  public opcion: string;
  currency: any;
  public message: string;
  public listMoney : string;

  public banco  : string = "";
  public number : number = 0;
  public propietario: string = "";
  public nick : string = "";
  public opcionMoneda: string='';
  public email : string='';

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public utils: UtilsProvider, public http: HttpProvider,
              public command: CommandsProvider) {
    this.condicion = false;

    //Banco seleccionado
    if(this.navParams.get("bancoSelected") != null){ this.banco = this.navParams.get("bancoSelected"); }

  }

  ionViewDidLoad() {
    //obtenci贸n de la lista guardada en sesi贸n para no volver hacer la consulta
    this.storage.get('listMoney').then((val) =>    { if(val != null ){ this.listMoney = val; } });
  }

  ionViewDidEnter() {
    if(this.listMoney == null || this.listMoney == undefined){
      this.loadMoneyBank();
    }
  }

  listBank(){
    this.navCtrl.push("ListBankPage");
  }

  oprima(){
    this.opcionMoneda = this.opcion;
    
    alert("Escogio "+this.opcion);
  }

  //Metodo para la carga del propietario en el campo account holder name
  notify(){
    this.value = this.value;

    if(this.value){
      this.condicion = true;
        //Obtenci贸n del usuario que inicio sesi贸n
        this.storage.get('usuario').then((val) => {
          if (val != null) {
            this.propietario = val;
          }
        });
    }
    else{
      this.condicion = false;
      this.propietario = "";
    }

  }
  
  //Valida en correo ingresado
  validarEmail(){

    this.email = this.email;
 
    if(this.utils.validar_email(this.email)) {
        //alert("El email es correcto");
    }
    else {
        //alert("El email NO es correcto");
        this.utils.alertCampos("transferProduct.validateEmail");
    }

  }

  private loadMoneyBank() {   
        
    
    //var command = 'E2ADMR81_1';
    var command :string[] =  this.utils.getCommands(this.command.E2ADMR81_1); 

    
    var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=null' + this.utils.separator + ' ' + this.utils.separator + this.utils.separator + 'E2NCNR01' + this.utils.separator + 'null' + this.utils.separator + '%BA%20&PAR=' + this.utils.separator;
    this.http.callServer(param).then(res => {      
    this.message = res.message;

      if (this.message != '' && typeof this.message!=="undefined") {
        this.utils.alertServidor(this.message);
      } else {
        this.listMoney = res.lista1;
        this.storage.set("listMoney", res.lista1);
        this.storage.set("listBank", res.lista2);
                
      }
    },
      error => {
        console.log(error);
      });
  }

  //Metodo para crear cuenta
  public validarDatos(){
  //alert("banco-> "+ (this.banco == undefined || this.banco == "") + "\n numero-> "+(this.number == undefined || this.number == 0)+ 
     //   "\n propietario-> "+(this.propietario == undefined || this.propietario == "")+ "\n moneda-> "+(this.opcionMoneda == undefined || this.opcionMoneda == ""));
     if( (this.banco == undefined || this.banco == "")             && (this.number == undefined || this.number == 0 ) &&
         (this.propietario == undefined || this.propietario == "") && (this.opcionMoneda == undefined || this.opcionMoneda == "") ){
           this.utils.alertCampos("productToPay.alert");
      }
      else if( (this.number == undefined || this.number == 0 ) && (this.propietario == undefined || this.propietario == "") && (this.opcionMoneda == undefined || this.opcionMoneda == "")){
           this.utils.alertCampos("productToPay.alert");
      }
      else{
        this.agregarCuentas();
      }
    
      if(this.email != "" && this.email != undefined){
            this.validarEmail();
      }

  }

  public cancelar(){ 
    //this.utils.cleanAll();
    this.navCtrl.push("TransferPage"); 
  }


  //Metodo para validar que solo ingrese numeros en el campo mediante js
  validaNumericos(event) {
    if (event.charCode >= 48 && event.charCode <= 57) {
      return true;
    }
    return false;
  }
  
  //Metodo para agregar cuentas
  public agregarCuentas(){

    if(this.banco != undefined) { var codBanco = this.banco; } else { codBanco = "" }
    if(this.number != undefined || this.number != 0 ) { var numcuenta = this.number; } else { numcuenta = 0 }
    if( this.propietario != undefined || this.propietario != "" ){ var nameHolder = this.propietario;} else { nameHolder = "" }
    if(this.opcionMoneda != undefined){ var moneda = this.opcionMoneda; } else { moneda = "" }
        
    var tipoId = 99;
    var numDocument = "%20";
    var nickName = this.nick;
    var email = this.email;
    
    var command = 'E2ADMR81_1';
    var param = command + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=null' + this.utils.separator + 'Enviar' + 
                this.utils.separator + this.utils.separator + 'E2LSMR79_1' + this.utils.separator + '0' + this.utils.separator + this.utils.separator + '&PAR=%20' + this.utils.separator +
                codBanco + this.utils.separator + numcuenta + this.utils.separator + moneda + this.utils.separator + "%20"+ this.utils.separator +
                nameHolder + this.utils.separator +tipoId+ this.utils.separator+ "%20"+ this.utils.separator+ "%20"+ this.utils.separator + nickName + this.utils.separator + email;
    this.http.callServer(param).then(res => {      
    this.message = res.message;

      if (this.message != '' && typeof this.message!=="undefined") {
        //alert(this.message);
        this.utils.alertCampos("addAccount.message");
        this.navCtrl.push("TransferPage");
      } else {
        //var res = res.lista1;                
      }
    },
      error => {
        console.log(error);
      });
  }

  
  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

}



